.fullscreen-loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.724); 
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000; 
  }
  
 
  .fullscreen-loader img {
    width: 50px; 
    height: 50px; 
    
  }